  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
* {
  font-size: 26px;
}
.nav {
  width: 100%;
  height: 98px;
  position: fixed;
  bottom: 0;
  left: 0;
  background: #eeeeee;
}
.nav li {
  float: left;
  width: 90px;
  height: 100%;
  margin-left: 120px;
  text-align: center;
  list-style: none;
}
.nav li i {
  display: block;
  font-size: 45px;
  width: 100%;
  height: 45px;
}
.nav li span {
  line-height: 60px;
}
.nav li:nth-of-type(1) {
  margin-left: 0;
}
.nav li.router-link-active {
  color: #f23030;
}
