#homepage {
  padding-bottom: 200px;
  width: 100%;
}
#homepage .head {
  width: 680px;
  height: 88px;
  margin: 0 auto;
}
#homepage .head .head_left {
  width: 102px;
  height: 100%;
  float: left;
  text-align: center;
}
#homepage .head .head_left img {
  margin-top: 10px;
}
#homepage .head .head_left p {
  font-size: 34px;
  line-height: 20px;
}
#homepage .head input {
  width: 453px;
  height: 58px;
  border: 1px solid #ccc;
  border-radius: 29px;
  padding-left: 80px;
  margin: 15px 0 0 20px;
  float: left;
  background: url(./static/img/home.eb61b66.png) no-repeat 20px center;
  background-size: 40px 40px;
  outline: none;
}
#homepage .head .head_right {
  width: 70px;
  height: 100%;
  float: right;
  text-align: center;
}
#homepage .head .head_right img {
  margin-top: 10px;
}
#homepage .head .head_right p {
  font-size: 34px;
  line-height: 20px;
}
#homepage .banner {
  width: 100%;
  height: 320px;
}
#homepage .nav-icon {
  width: 100%;
  height: 132px;
  margin-top: 20px;
}
#homepage .nav-icon li {
  width: 138px;
  height: 100%;
  float: left;
  text-align: center;
  list-style: none;
  margin-left: 7px;
}
#homepage .nav-icon li div {
  width: 94px;
  height: 94px;
  float: left;
  margin: 0 22px;
  border-radius: 6px;
  padding-top: 15px;
}
#homepage .nav-icon li:nth-of-type(1) {
  margin-left: 0;
}
#homepage .customization {
  width: 100%;
  height: 300px;
  padding-left: 20px;
}
#homepage .customization h3 {
  width: 100%;
  height: 60px;
  font-size: 30px;
  line-height: 60px;
  font-weight: normal;
  padding-left: 25px;
  position: relative;
}
#homepage .customization h3 span {
  width: 120px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  float: right;
  background: #ffc001;
  border-radius: 15px;
  margin: 17px 20px 0 0;
}
#homepage .customization h3:before {
  content: '';
  width: 8px;
  height: 26px;
  background: #f65a4e;
  position: absolute;
  top: 17px;
  left: 0;
}
#homepage .customization .swiper-container {
  width: 100%;
  height: 240px;
  text-align: center;
}
#homepage .customization .swiper-container .swiper-slide {
  width: 150px!important;
  height: 100%;
  margin-right: 54px!important;
  position: relative;
}
#homepage .customization .swiper-container .swiper-slide img {
  width: 150px;
  height: 150px;
}
#homepage .customization .swiper-container .swiper-slide p {
  color: red;
  line-height: 26px;
  margin: 0;
  padding: 0;
